import React, { Suspense } from "react";
import BasicExample from "./examples/BasicExample";
import PremiumExample from "./examples/PremiumExample";
import PlotExample from "./examples/PlotExample";

class App extends React.Component{
  state = {
    hide: true
  };

  onButtonClick = () => {
    this.setState({
      hide : !this.state.hide
    });
  };

  getButtonName = (hide: boolean) => hide ? "hide" : "show";

  render(){
    const {hide} = this.state;
    return (
      <div>
        <Suspense fallback={null}>
          <div>
            <BasicExample />
          </div>
          <div>
            <button onClick={this.onButtonClick}>
              {this.getButtonName(this.state.hide)}</button>
            {hide && <PremiumExample />}
          </div>
          <div>
            <h2>Plot</h2>
            <PlotExample />
          </div>
        </Suspense>
      </div>
    );
  };
}

